@import 'sources/styles/media-mixin.scss';

.dontMiss {
  margin: 0 auto;
  padding: 0 70px 38px;
  margin-bottom: 100px;
  overflow: hidden;
  max-width: 1440px;

  @include _1920 {
    padding: 0;
  }

  @include _1440 {
    padding: 0 70px 38px;
  }

  @include _960 {
    padding: 31px 20px 24px;
  }

  @include _600 {
    margin: 16px auto 30px;
    padding: 15px 0 12px;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1992px;
    position: relative;
    left: 0;
    transition: all 0.5s ease;

    .column {
      display: flex;
      justify-content: space-between;

      @include _768 {
        flex-direction: column;
        min-width: 100%;
        margin-right: 40px;
      }

      @include _600 {
        margin-right: 0;
      }
    }

    @include _768 {
      width: 100%;
      margin: 0 auto;
    }
  }
}