@import 'sources/styles/variables.scss';
@import 'sources/styles/media-mixin.scss';

.inputWrapper {
  display:  flex;
}

.range {
  width: 100%;
  margin-top: 24px;

  @include _600 {
    margin: 16px;
    width: 91%
  }
}

.range[type='range'] {
  -webkit-appearance: none;
}

.range[type='range']:focus {
  outline: none;
}

.range[type='range']::-webkit-slider-runnable-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  background: #bdbdbd;
  border-radius: 25px;
  border: 0px solid #000101;
  border-radius: 100%;
}

.range[type='range']::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 8px;
  width: 50%;
  background-color: var(--input-thumb-color);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -1.6px;
  border-radius: 60px;
}

.small.range[type='range']::-webkit-slider-thumb {
  width: 15%;
}

.middle.range[type='range']::-webkit-slider-thumb {
  width: 30%;
}

.big.range[type='range']::-webkit-slider-thumb {
  width: 50%;
}

.range[type='range']:focus::-webkit-slider-runnable-track {
  background: #bdbdbd;
}

.range[type='range']::-moz-range-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  background: #bdbdbd;
  border-radius: 25px;
  border: 0px solid #000101;
  border-radius: 60px;
}

.range[type='range']::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 8px;
  background: #bdbd;
  cursor: pointer;
  border-radius: 60px;
}

.range[type='range']::-ms-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 39px 0;
  color: transparent;
  border-radius: 60px;
}

.range[type='range']::-ms-fill-lower {
  background: #bdbdbd;
  border: 0px solid #000101;
  border-radius: 50px;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
}

.range[type='range']::-ms-fill-upper {
  background: #bdbdbd;
  border: 0px solid #000101;
  border-radius: 50px;
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
}

.range[type='range']::-ms-thumb {
  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  border: 0px solid #000000;
  height: 8px;
  background: #bdbd;
  cursor: pointer;
  border-radius: 60px;
}

.range[type='range']:focus::-ms-fill-lower {
  background: #bdbdbd;
}

.range[type='range']:focus::-ms-fill-upper {
  background: #bdbdbd;
}